<template>
	<view class="">
		<view class=""> 
			<image class="bgc-picture1" src="../../static/fristPage/bgcL.png" mode="widthFix"></image>
			<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
			<view class="" style="height: 290rpx;"></view>
			<view v-if="subOkFlag" class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 783rpx;">
				<view  class="" style="position: absolute; top: 130rpx; left: 78rpx;"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image @click="backRegister" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
				</view>
				<view class="" style="height: 70rpx;"></view>
				<view class="f49rpx color-H0f649c ta-c fw800 lsp-5" style="">
					Subscription Success
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 40rpx 40rpx 29rpx;">
					You have successfully set up your TekyGo! subscription! 
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 0rpx 20rpx 40rpx;">
					You can now take a look through the app, or skip to login.
				</view> 
				<view class="" style="margin: 0 21rpx 40rpx; height: 220rpx;">
					<view class="" v-show="clickVideo" style="position: relative;">
						<image src="../../static/fristPage/videoImg.png" 
						mode="" style="width: 100%; height: 220rpx; z-index: 2;"></image> 
						<view class="" style="position: absolute; top: 62rpx; left: 237rpx; z-index: 3;">
							<image @click="videoPlay" src="../../static/Index/PlayButtoncopy3.png" 
							mode="widthFix" style="width: 96rpx;"></image>
						</view>
					</view> 
					<video v-if="!clickVideo" style="width: 100%; height: 220rpx;" 
					id="submyVideo"
					@pause="videoStops"
					@ended="videoStops"
					:src="url" 
					:autoplay="true"
					:controls="true"
					:show-fullscreen-btn="true"
					:enable-progress-gesture="false"
					:show-play-btn="true"
					:show-center-play-btn="false"
					object-fit="fill"
					></video>
				</view>
				<view class="ta-c bgc-o-r FFF fw800 f33rpx " @click="goIndex"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="border-radius: 42rpx; line-height: 84rpx; height: 84rpx; letter-spacing: 2rpx; margin: 0 45rpx 23rpx;">
					Skip tour
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				subOkFlag:true,
				clickVideo:true,
				url:"https://tekygo-1.s3.us-east-2.amazonaws.com/tekygo01/files/20211015/1634286674449.mp4"
			}
		},
		created() {
			this.getDate()
			this.videoContext=uni.createVideoContext('submyVideo',this)
		},
		methods:{
			async getDate(){
				let _this = this
				const res = await this.$myRequest({
					url:'/api/sub/conf'
				})
				console.log('视频');
				if(res.data.succeeded){
					_this.url = res.data.data.video
				}
			},
			sss(){
				console.log('点击');
			},
			backRegister(){
				this.videoContext.pause()
				uni.reLaunch({
					url:'./subscription'
				})
			},
			videoPlay(){
				this.clickVideo = false
				this.videoContext.play()
			},
			videoStops(){
				this.videoContext.pause()
				this.clickVideo = true
			},
			goIndex(){
				uni.setStorageSync('isLogin',true)
				this.videoContext.pause()
				let ifBind = uni.getStorageSync('ifBind')
				console.log(ifBind);
				if(ifBind){
					uni.switchTab({
						url:"../index/index"
					})
				}else{
					uni.redirectTo({
						url:'./Bind'
					})
				}
			}
		}
	}
</script>

<style scoped>
	.bgc-picture1{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
</style>
